import { Component, OnInit } from '@angular/core';
import { BillData } from 'src/app/user/models/bill-data.model';
import { Product } from 'src/app/user/models/product.model';
import { Recipient } from 'src/app/user/models/recipient.model';
import { UserService } from 'src/app/user/services/user.service';

@Component({
  selector: 'app-sale-order-step-one',
  templateUrl: './sale-order-step-one.component.html',
  styles: [],
})
export class SaleOrderStepOneComponent implements OnInit {
  open: boolean = false;
  public products: Product[] | undefined = [];
  public total: number | undefined = 0;
  public billdata: BillData | undefined = undefined;
  public recipients: Recipient[] | undefined = [];
  Popup: any[] = [
    {
      selection: 'Pickup',
      title: 'Pickup - NO TIENE COSTO',
      description:
        'Retiro de productos en el warehouse Bymia en Miami. Almacenamos sin costo tu compra durante TREINTA (30) das. La empresa de envos estar a cargo del cliente. En caso de envo internacional,  nuestro customer te solicitar el Certificado de Exportacin.',
    },
    {
      selection: 'Envos internacionales',
      title: 'Envos nacionales e internacionales ',
      description:
        '1) Envos internacionales (no abonan tax) \n a) Destinos Argentina y Repblica Dominicana: solo por MiaCargo. \n Tu envo ser facturado y lo pagars en destino. Desde tu panel podrs \n seguir su trayectoria con el nmero de tracking.\n b) Otros destinos internacionales Envo con DHL: abonars tu envo \n junto con tu compra y podrs seguir su trayectoria con el nmero de \n tracking desde tu panel de cliente. \n Para conocer los costos del envo a tu pas, consultale a uno de nuestros \n representantes de  atencin al cliente. \n c) Envos nacionales (abonan tax) \n + Entregas a domicilio en Miami por USPS. \n Pags el envo junto con tu factura de compra. \n + Entregas a domicilio en USA, menos Miami por DHL. \n Abons tu envo junto con la compra y podrs seguir su trayectoria \n con el nmero de tracking asignado desde tu panel de cliente.',
    },
  ];
  constructor(private userService: UserService) {
    console.log('Order', this.userService.getOrder());
    this.billdata = this.userService.getOrder().bill_data;
    this.recipients = this.userService.getOrder().recipients;

    this.products = this.userService.getOrder().items?.map(item => {
      return { ...item, subtotal: item.quantity * item.price };
    });
    console.log('Products:', this.products);
    this.total = this.products?.reduce((acc, cur) => acc + cur.subtotal, 0);
    console.log('Total', this.total);
  }

  endOrder() {
    this.userService.endOrder().subscribe(res => {
      console.log('resp:', res);
    });
  }

  ngOnInit(): void {}
}
